<template>
  <img width="300" alt="Vue logo" src="./assets/1.jpg">
  <h1>{{ firstname }} {{ lastname }}</h1>
  <button v-on:click="showalertf">แสดงชื่อ</button>
  <button @click="showalertl">แสดงนามสกุล</button>
  <h1>{{ age }}</h1>
  <button @click="plus">เพิ่มตัวเลข</button>
  <button @click="minus">ลบตัวเลข</button><br>
  <button @click="plus5(5)">เพิ่มตัวเลขทีละ 5</button>
  <button @click="minus5(5)">ลบตัวเลขทีละ 5</button>
  <br>
  <br>
  <form action="">
    <label for="">กรุณากรอกชื่อเล่น : </label>
    <input type="text" v-on:input="setnickname"><br>
    <label for="">กรุณากรอกชื่อจริง : </label>
    <input type="text" v-on:input="setfname"><br>
    <label for="">กรุณากรอกนามสกุล : </label>
    <input type="text" v-on:input="setlname"><br>
    <label for="">กรุณากรอกอายุ : </label>
    <input type="text" v-on:input="setages"><br>
    <label for="">กรุณากรอกวันเกิด : </label>
    <input type="text" v-on:input="setbdate"><br>
    <label for="">กรุณากรอกสาขา : </label>
    <input type="text" v-on:input="setIT"><br>
    <label for="">กรุณากรอกห้อง : </label>
    <input type="text" v-on:input="setroom"><br>
    <label for="">กรุณากรอกคติประจำใจ : </label>
    <input type="text" v-on:input="setkti"><br>
    <label for="">กรุณากรอกสถานที่เรียนต่อ : </label>
    <input type="text" v-on:input="setschool"><br>
    <label for="">กรุณากรอกวิชาที่ไม่ชอบ : </label>
    <input type="text" v-on:input="setdlsub"><br>
  </form>
  <h2>แสดงชื่อเล่น : {{ nickname }}</h2>
  <h2>แสดงชื่อจริง : {{ fname }}</h2>
  <h2>แสดงชื่อนามสกุล : {{ fname }} {{ lname }}</h2>
  <h2>แสดงอายุ : {{ ages }}</h2>
  <h2>แสดงวัน/เดือน/ปี : {{ bdate }}</h2>
  <h2>แสดงสาขา : {{ IT }}</h2>
  <h2>แสดงห้อง : {{ room }}</h2>
  <h2>แสดงคติประจำใจ : {{ kti }}</h2>
  <h2>แสดงสถานที่เรียนต่อ : {{ school }}</h2>
  <h2>แสดงวิชาที่ไม่ชอบ : {{ dlsub }}</h2>
</template>

<script>
export default {
  name: 'App',
   data(){
    return{
      firstname : "Suphaloek",
      lastname : "Khamkoon",
      age: 20,
      nickname : "",
      fname:"",
      lname:"",
      ages:"",
      bdate:"",
      IT:"",
      room:"",
      kti:"",
      school:"",
      dlsub:"",
    }
   },
   methods:{
    showalertf(){
      alert(this.firstname)
    },
    showalertl(){
      alert(this.lastname)
    },
    plus(){
      this.age++
    },
    minus(){
      this.age--
    },
    plus5(val){
      this.age+=val
    },
    minus5(val){
      this.age-=val
    },
    setnickname(event){
      this.nickname=event.target.value
    },
    setfname(event){
      this.fname=event.target.value
    },
    setlname(event){
      this.lname=event.target.value
    },
    setages(event){
      this.ages=event.target.value
    },
    setbdate(event){
      this.bdate=event.target.value
    },
    setIT(event){
      this.IT=event.target.value
    },
    setroom(event){
      this.room=event.target.value
    },
    setkti(event){
      this.kti=event.target.value
    },
    setschool(event){
      this.school=event.target.value
    },
    setdlsub(event){
      this.dlsub=event.target.value
    }
  }
}

</script>

